import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Edit, Clear } from '@mui/icons-material';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import Parse from '../vendor/Parse'
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button'
import Textarea from '../components/Textarea';
const { useState } = React;
const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
})(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
    }),
}));

export default function RecipeReviewCard({ list = [], isAuth, editPost, handleDelete, onPost }) {


    const [expanded, setExpanded] = React.useState(false);
    const [newPostPayload, setNewPostPayload] = useState({})
    const [editingPost, setEditingPost] = useState({})

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };



    return (
        <>


            {
                list?.map((post) => <Card>
                    <CardHeader
                        avatar={
                            <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                A
                            </Avatar>
                        }
                        action={
                            isAuth && <><IconButton aria-label="settings" onClick={() => handleDelete(post)}>
                                <Clear />
                            </IconButton>
                                <IconButton aria-label="settings" onClick={() => setNewPostPayload({
                                    itemId: post.objectId,
                                    objectId: post.objectId,
                                    ...post.data
                                })}>
                                    <Edit />
                                </IconButton>
                            </>
                        }
                        title={post?.data?.title}
                        subheader={post.createdAt.toString()}
                    />
                    <Typography variant="body2" color="text.secondary" style={{ padding: '16px' }}>
                        {ReactHtmlParser(post?.data?.content)}
                    </Typography>
                    {
                        post?.data?.image && <CardMedia
                            component="img"
                            height="294"
                            image={post?.data?.image}
                            alt="Paella dish"
                        />
                    }

                    {
                        post.data?.video && <div class="youtube-video-container">
                            <iframe width="560" height="315" src={post.data?.video} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
                        </div>
                    }
                    <CardContent>

                    </CardContent>

                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <CardContent>
                            <Typography paragraph>Method:</Typography>
                            <Typography paragraph>

                            </Typography>
                            <Typography paragraph>

                            </Typography>
                            <Typography paragraph>

                            </Typography>
                            <Typography>

                            </Typography>
                        </CardContent>
                    </Collapse>
                </Card>)
            }
            {
                isAuth && <Card>
                    <CardHeader
                        avatar={
                            <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                A
                            </Avatar>
                        }
                        title={newPostPayload.title}
                        subheader={""}
                    />
                    <Typography variant="body2" color="text.secondary" style={{ padding: '16px' }}>
                        {ReactHtmlParser(newPostPayload?.content)}
                    </Typography>
                    {
                        newPostPayload?.image && <CardMedia
                            component="img"
                            height="294"
                            image={newPostPayload.image}
                            alt=""
                        />
                    }

                    {
                        newPostPayload?.video && <div class="youtube-video-container">
                            <iframe width="560" height="315" src={newPostPayload?.video} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
                        </div>
                    }

                    <CardContent>

                        <Typography variant="body2" color="text.secondary">
                            <Box>
                                <TextField

                                    value={newPostPayload.title}
                                    fullWidth onChange={(e) => {
                                        setNewPostPayload({
                                            ...newPostPayload,
                                            title: e.target.value,
                                        })
                                    }} />
                                <Textarea
                                    value={newPostPayload.content}
                                    fullWidth onChange={(e) => {

                                        setNewPostPayload({
                                            ...newPostPayload,
                                            content: e.target.value
                                        })
                                    }} />
                                <TextField
                                    value={newPostPayload.video}
                                    fullWidth placeholder="Video Url" onChange={(e) => {

                                        setNewPostPayload({
                                            ...newPostPayload,
                                            video: e.target.value
                                        })

                                    }} />
                            </Box>

                        </Typography>
                    </CardContent>
                    <CardActions disableSpacing>
                        <Button
                            component="label"
                            role={undefined}
                            variant="contained"
                            tabIndex={-1}

                        >
                            Upload file
                            <VisuallyHiddenInput type="file" onChange={(event) => {

                                const file = event.target.files[0];
                                const reader = new FileReader();
                                reader.onloadend = () => {
                                    setNewPostPayload({
                                        ...newPostPayload,
                                        image: reader.result
                                    })
                                };
                                reader.readAsDataURL(file);
                            }} />
                        </Button>
                        <ExpandMore
                            expand={expanded}
                            onClick={handleExpandClick}
                            aria-expanded={expanded}
                            aria-label="show more"

                        >
                            <ExpandMoreIcon />
                        </ExpandMore>
                        <Button onClick={() => onPost && onPost(newPostPayload)}>
                            Post
                        </Button>
                    </CardActions>
                </Card>
            }
        </>

    );
}